export default function Search (){
    return (
        <>
            <form >
                <button type="submit" >
                    <span >Search</span>
                </button>
                
                <input
                    className="input"
                    type="text"
                    name="value"
                    autoComplete="off"
                    autoFocus
                    placeholder="Search images and photos"
                />
            </form>
        </>
    )
}