import {useState} from "react"

export default function Home() {
const[films, setFilms]=useState([])

    fetch('https://api.themoviedb.org/3/trending/all/day?api_key=6335ce0a24f472a8f252fc29a6ddd36b&language=en-US&page=1')
        .then(res => res.json())
        .then(res => setFilms(res.results))
    
    return (
        <ul>
            {films.map((film) => {
                return (
                    <li key = {film.id} >{film.title?film.title:film.name}</li>
                )
            })}
        </ul>
    )
}